document.addEventListener('DOMContentLoaded', function() {
    crearGaleria();
});

function crearGaleria(){
    // Selecciono el la clase "galeria-imagenes" del html
    const galeria = document.querySelector('.galeria-imagenes');

    for(let i = 1 ; i <= 12; i++){
        // Crea un elemento imagen en el html
        const imagen = document.createElement('IMG');
        //imagen.src = `build/img/thumb/${i}.webp`;
        imagen.src = `build/img/thumb/${i}.jpg`; // asigno la ruta de la imagen que quiero tomar
        imagen.dataset.imagenId = i; // Agrega el atributo "data-imagen-id" al hmtl

        // Evento que reconoce cuando das click en el elemento
        imagen.onclick = mostrarImagen;

        // Creamos una lista en el html
        const lista = document.createElement('LI');
        lista.appendChild(imagen); // Agregamos las imagenes a la lista

        galeria.appendChild(lista); // Agregamos la lista a la galeria
    }
}

function mostrarImagen(e){
    // Obtiene el id de la imagen
    const id = parseInt(e.target.dataset.imagenId);
    
    // Genera la imagen en grande 
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.jpg`;
    
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('galeria-overlay');

    // Boton para cerrar la imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add("btn-cerrar");

    // Cuando se da click se cierra la imagen
    overlay.onclick = function(){
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    // Cuando se presiona se cierra la imagen
    cerrarImagen.onclick = function(){
        overlay.remove(); // Elimina el overlay
        body.classList.remove('fijar-body');
    }

    overlay.appendChild(cerrarImagen);
    
    // Mostrar en el html
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}